.landing-container {
    display: flex;
    min-height: 90vh;
    justify-content: space-between;
    align-items: center;
    color: #eee2ee;
    padding: 0 32px;
    font-family: "Bangers", cursive;
    margin: 0;
    flex-wrap: wrap;
    flex-direction: row;
}

.landing-header {
    font-size: 92px;
    letter-spacing: 4px;
    margin: 0;
}

.typewriter-container {
    font-size: 80px;
    letter-spacing: 4px;
}

.flash-image {
    width: 40vw;
}

 @media (max-width: 800px) {
    .landing-container {
        flex-direction: column;
        align-items: center;
    }
}

